import React, {Component} from "react";
import {OraFloatingSelect} from "eshop/components/OraCommonComponents";

export default class RepresentationMode extends Component {
    constructor(props) {
        super(props);
    }

    defaultMode() {
        if(this.props.defaultMode && this.props.options.map(o=>o.value).indexOf(this.props.defaultMode) > -1) {
            return this.props.defaultMode;
        } else {
            return this.props.options[0].value;
        }
    }

    getConfigForMode(mode){
        return this.props.modesConfig.filter(c => c.mode == mode)[0]
    }

    reset() {
        let defaultMode = this.defaultMode();
        this.props.onChange(defaultMode, this.getConfigForMode(defaultMode));
    }

    componentWillMount() {
        this.reset();
    }

    componentDidUpdate() {
        if(this.props.options.map(o=>o.value).indexOf(this.props.selected) == -1) {
            this.reset();
        }
    }

	getPropsForSelect(name) {
        return {
            name,
            id: name,
            onChange: this.onSelectChange.bind(this),
            withEmptyOption: false,
            label: "Forma reprezentacji",
            disabled: false,
            className: "u-w-100",
            value: this.props.selected
        };
    }

    onSelectChange(event) {
        this.props.onChange(event.value, this.getConfigForMode(event.value))
    }

    render() {
        return (
             <OraFloatingSelect {...this.getPropsForSelect('representationMode')} options={this.props.options}/>
        );
    }
}